.firsb {
  position: relative;
  background: white; }
  .firsb:after {
    content: "";
    position: absolute;
    background: url("../img/first.jpg") center/cover no-repeat;
    height: 100vh;
    width: 50vw;
    right: 0;
    top: 0; }
  .firsb__title {
    font-size: 48px;
    color: black;
    max-width: 450px; }
  .firsb__desc {
    font-size: 16px;
    line-height: 160%;
    color: black;
    font-family: "Circe";
    max-width: 400px;
    margin-top: 83px;
    margin-bottom: 70px; }
  .firsb__wrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    min-height: 100vh; }
  .firsb__block {
    max-width: 50%; }
